@import url(https://fonts.googleapis.com/css2?family=Signika:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#ECEFF1;
}

h1 {
  font-family: 'Signika', sans-serif;
}

.App-header {
    text-align:center;
    font-size: calc(10px + 2vmin);
    color: #282C34;
  }

  h1 {
    color: #25C38A;
  }
form {
    display:flex;
    grid-gap: 10px;
    gap: 10px;
    width:80%;
    margin:0 auto;
}

label {
    display:flex;
    flex-direction:column;
    grid-gap:2px;
    gap:2px;
}

input {
    font-size:1.4em;
}

input[type="submit"] {
    background: #25C38A;
    color: #FFF;
    border: 1px solid #25C38A;
    border-radius: 5px;
    padding:3px 20px;
}
table{margin:50px 10px;width:100%}tbody th{border:1px solid #CCC;text-align:center;font-size:0.8em;font-weight:bold;padding:5px;background:#FFF}tbody td{text-align:center;font-weight:bold;font-size:2em;padding:20px 5px;border:1px solid #CCC;background:#FFF}tbody td:hover{background:#25C38A;color:#FFF;cursor:pointer}tfoot td{font-weight:normal;color:#666;padding:5px;text-align:center}.items{display:flex}.items dl{width:320px;background:#FFF;border-radius:5px;padding:20px 0;font-size:0.9em}.items dt{color:#666;display:block;float:left;width:180px;margin:0 10px 5px 0;padding:0;text-align:right}.items dd{display:block;margin-bottom:5px;font-weight:bold}

div.item{width:220px;background:#FFF;margin:5px;border-radius:5px;padding:5px}div.item img{width:220px}div.item p{color:#666}div.item p.label{font-size:0.6em;font-weight:bold;margin:2px 0;color:#25C38A}

